import React from "react";
import Figure from "react-bootstrap/esm/Figure";
import "./Aboutfoot.css";

const Aboutfoot = (props) => {
  return (
    <div>
      <Figure>
        <Figure.Image
          width={171}
          height={180}
          alt="171x180"
          src={props.img}
          className="productimg"
        />
        <Figure.Caption className="text-center ">
          <h4> {props.title}</h4>
        </Figure.Caption>
        <h6 className="text-center">
          {" "}
          <strike> {props.priveousprice}</strike> {props.price}
        </h6>
      </Figure>
    </div>
  );
};

export default Aboutfoot;
